.header {
    position: fixed;
    z-index: 99999;
    top: 0;
    left: 0;

    width: 100%;
    height: 72px;

    background: #FCFCFC;
    box-shadow: 0 2px 10px rgba(109, 109, 109, 0.25), inset 0 1px 0 rgba(255, 255, 255, 0.3);

}

.container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    max-width: 1000px;
    margin: 0 auto;
}

.logo {
    cursor: pointer;

    height: 100%;
}

.navbar {
    padding: 5px;

    font-family: Montserrat, serif;
    font-size: 14px;
}

a {
    padding: 5px;

    color: black;
    text-decoration: none;
}

a:hover {
    color: #42a5f5;
}

/* ------------ user -------------- */
.user {
    position: relative;

    display: flex;
    align-items: center;
    justify-content: center;

    min-width: 150px;
    height: 100%;
}

.user_name {
    cursor: pointer;
    user-select: none;

    box-sizing: content-box;
    margin-right: 10px;

    border: 1px dashed rgba(255, 255, 255, 0);
}

.user_name:hover {
    cursor: pointer;

    border-bottom: 1px dashed #366EFF;
}

.user_photo {
    position: relative;

    overflow: hidden;

    width: 60px;
    max-width: none;
    height: 60px;
    padding-right: 0;
    padding-left: 0;

    border-radius: 50%;
}

.user_photo img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: 60px;
    height: 60px;

    object-fit: cover;

}

.user_photo img:hover {
    cursor: pointer;
}

.selector {
    position: absolute;
    top: 75px;

    display: flex;
    flex-direction: column;

    width: 150px;
    padding: 10px;

    background-color: #ffffff;
    box-shadow: 0 2px 10px rgba(109, 109, 109, 0.25), inset 0 1px 0 rgba(255, 255, 255, 0.3);
}